<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">{{ $t('Manage Status Mapping') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        MapStatusId: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            serviceAppointmentStatusList: [],
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Status Mapping Name'),
                            name: "StatusMappingName",
                            value: "",
                            placeholder: this.$t('Enter Status Mapping Name'),
                            validationRules: { "required": true, max: 100 },
                        },                        
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            placeholder: this.$t('Enter Description'),
                            validationRules: { "required": false, max: 100 },
                        },   
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            mode: "tag",
                            label: this.$t('Service Appointment Status '),
                            name: "ServiceUser",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select Status'),
                            validationRules: { "required": true, max: 100 },
                        },

                        {
                            astype: "SelectField",
                            mode: "tag",
                            label: this.$t('Work Order Status '),
                            name: "WorkOrder",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select Status'),
                            validationRules: { "required": true, max: 100 },
                        },

                        {
                            astype: "SelectField",
                            mode: "tag",
                            label: this.$t('Ticket Status '),
                            name: "TicketStatus",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select Status'),
                            validationRules: { "required": true, max: 100 },
                        },
                    ],
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ],
            ListData: []
        }
    },
    created: async function () {
        this.BindServiceAppointmentStatusType();
        this.BindWorkOrderStatusType();
        this.BindTicketStatusType();        
    },
    methods:{
        Close: function () {
            this.$parent.AddMapStatusPopup = false;
        },
        BindServiceAppointmentStatusType: async function(){
            debugger;
            var vm = this;
            var appendingUrl = `id=&companyId=${vm.companyId}&userId=${vm.userid}&moduleName=${'FSM'}&type=${'SERVICEAPPOINTMENT_STATUS'}`;
            await DataServices.GetDDLData(appendingUrl).then(function (response) {
                debugger;
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.DATA.forEach((item, index) => {
                       vm.FormSchema[2].Data[0].config.options.push({  
                        name: item.name,                      
                        value: item.value                                              
                    })
                  })
                }
               }
            });
        },
        BindWorkOrderStatusType: async function(){
            debugger;
            var vm = this;
            var appendingUrl = `id=&companyId=${vm.companyId}&userId=${vm.userid}&moduleName=${'FSM'}&type=${'WORK_ORDER_FINAL_STATUS'}`;
            await DataServices.GetDDLData(appendingUrl).then(function (response) {
                debugger;
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.DATA.forEach((item, index) => {
                       vm.FormSchema[2].Data[1].config.options.push({
                        value: item.value,
                        name: item.name
                    })
                  })
                }
               }
            });
        },
        BindTicketStatusType: async function(){
            debugger;
            var vm = this;
            var appendingUrl = `id=&companyId=${vm.companyId}&userId=${vm.userid}&moduleName=${'FSM'}&type=${'FSM_TICKETING_STATUS'}`;
            await DataServices.GetDDLData(appendingUrl).then(function (response) {
                debugger;
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.DATA.forEach((item, index) => {
                       vm.FormSchema[2].Data[2].config.options.push({
                        value: item.value,
                        name: item.name
                    })
                  })
                }
               }
            });
        },
        onSubmit(formfields, val) {
            debugger;
            var vm = this;           
            var i = 0;
            
            var jsonobj = {
                statusMappingName: formfields.StatusMappingName,
                description: formfields.Description,
                serviceAppointmentStatus: formfields.ServiceUser,
                workOrderStatus: formfields.WorkOrder,
                ticketStatus: formfields.TicketStatus
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SaveMapStatusTicketType(params).then(function (response)  {
                debugger
                if (response.data.status == 'Success') {
                    if (response.data.result == "NAME_ALREADY_EXISTS") {
                        vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("StatusMapping")], [vm.$t("Name")]), "warning", true, vm.$t("Alert"));
                        vm.Close();
                    }
                    else if (response.data.result == "MAPPING_ALREADY_EXISTS") {
                        vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("StatusMapping")]), "warning", true, vm.$t("Alert"));
                    }
                    else if (response.data.result == "SA_STATUS_ALREADY_EXISTS") {
                        vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("ServiceAppointment")], [vm.$t("Status")]), "warning", true, vm.$t("Alert"));
                    }
                    else {
                        vm.ShowAlert(vm.$t("OffHoursSuccess", [vm.$t("StatusMapping")]), "success", true, vm.$t("Alert"));
                    }
                    vm.Close();
                    vm.$parent.FetchData();
                }
                else {
                    vm.ShowAlert(vm.$t("DeletedError", [vm.$t("StatusCode")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
   
}
</script>