async function  bindleftCommonSearchdropdown(instance, moduleName, filtername,data,AllCount) {
    var vm = instance;
var param = "moduleName='"+moduleName+"'&type="+filtername+""
var param1 = "moduleName=FSM&type="+filtername+""
if (filtername == 'SERVICEAPPOINTMENT_STATUS') {    
    await DataServices.GetDDLData(param1).then((response) => {
        
        response.data.DATA.forEach(element => {
            let obj = {
                name: element.name,
                value: element.value
            }
            vm.leftSearchFields[1].listOptions.push(obj);           
        });
    });
}
else if(filtername=='WORK_ORDER_FINAL_STATUS'){       
    await DataServices.GetDDLData(param1).then((response) => {
        
        response.data.DATA.forEach(element => {
            let obj = {
                name: element.name,
                value: element.value
            }
            vm.leftSearchFields[2].listOptions.push(obj);
        });
    });
}
else if(filtername=='FSM_TICKETING_STATUS'){ 
    await DataServices.GetDDLData(param1).then((response) => {
        response.data.DATA.forEach(element => {
            let obj = {
                name: element.name,
                value: element.value
            }
            vm.leftSearchFields[3].listOptions.push(obj);
        });
    });
}
}
export default { buttons, leftsearchSchema,bindleftCommonSearchdropdown};