<template>
    <section>
        <tg-list v-show="IsGroupView" :IsShowAction="false" ModuleName="Ticketing" SubModuleCode="FSM_Map_Status"
        IdentityColumn="id" :HeaderText="$t('Map Status')" :LegendArray="[]" :RenderRowActionMethod="() => { }"
        :widgets="[]" :listType="['List']" :ListData="MapStatusListing" :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
        :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchData"
        :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
        v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
        v-on:HeaderButtonClick="commonHeaderButtonClick">
        <template v-slot:slotdata="{ data }">
            <template v-if="data.column.COLUMN_NAME == 'status_id'">
                <listing-status :isfor="'MapStatus'" v-if="isCRMShowStausDrop(data.row.statusId, 'Ticketing')"
                    ref="updatestatus" v-bind:dataid="data.row.id" data-controller="Crm"
                    v-bind:statuslist="StatusListForListing" v-bind:item="data.statusId" data-action="index"
                    v-bind:dataselected="data.row.status_id==1001?'Active':'Inactive'" v-bind:callbackfunction="FetchData"
                    v-bind:isselectedvaluestring="true">
                </listing-status>
                <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.status_id, 'Ticketing')">
                    <span v-bind:class="getStatusClassList(data.row.status_id, 'status_id')">
                        {{ data.row[data.column.COLUMN_NAME] }}
                    </span>
                </a>
            </template>
        </template>       
    </tg-list>   
    <AddMapStatusPopup v-if="AddMapStatusPopup" :MapTypeId="mapStatusId"></AddMapStatusPopup>   
    </section>  
</template>
<script>
import Schema from "@/views/MapStatus/MapStatusSchema";
import DataServices from "@/services/DataService";
import AddMapStatusPopup from '../../components/Modal/AddMapStatusPopup.vue'
export default {
    components:{AddMapStatusPopup},
    data() {
        return {
            MapStatusListing: [],
            AddMapStatusPopup: false,
            mapStatusId: 0,
            TotalRecords: 0,
            PageSize: 10,
            PageNumber: 1,
            CurrentPage: 1,
            SortBy: '',
            SortExp: '',
            user_type: null,
            user_id:null,
            companyId:null,
            Headers: [{
                "COLUMN_NAME": "name",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Name",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "sa_status_id",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Service Appointment Status",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "wo_status_id",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Work Order Status",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "ticket_status_id",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Ticket Status",
                "DISPLAY_ORDER": 4
            },
            {
                "COLUMN_NAME": "created_by",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Created By",
                "DISPLAY_ORDER": 5
            },
            {
                "COLUMN_NAME": "created_at",
                "DATA_TYPE": "date",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Created At",
                "DISPLAY_ORDER": 5
            },
            {
                "COLUMN_NAME": "status_id",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 5
            }

            ],
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            IsGroupView: true,
            noRecord: false,
            StatusListForListing: [{ STATUS: 'Active', STATUS_ID: 1001 ,STATUS_COLOR:"#ffffff",STATUS_BG_COLOR: "#28a745"}, 
            { STATUS: 'Inactive', STATUS_ID: 1002 ,STATUS_COLOR:"#ffffff",STATUS_BG_COLOR:"#f50606"}],
        }
    },
    created: async function () {
        var vm = this;
        this.user_id = this.GetUserInfo.ID
        this.user_type = this.GetUserInfo.UserType
        this.companyId = this.GetUserInfo.CompanyId;
        await this.FetchData();
        await Schema.bindleftCommonSearchdropdown(vm,"FSM","SERVICEAPPOINTMENT_STATUS");
        await Schema.bindleftCommonSearchdropdown(vm,"FSM","WORK_ORDER_FINAL_STATUS");
        await Schema.bindleftCommonSearchdropdown(vm,"FSM","FSM_TICKETING_STATUS");
    },

    methods: {
        paramsToObject(entries) {
            const result = {}
            for (const [key, value] of entries) { // each 'entry' is a [key, value] tupple
                result[key] = value;
            }
            return result;
        },       
        leftCommonSearch: function (value) {
           // debugger;
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        FetchData: async function () {
            // debugger;
            var vueObj = this;           
            var srchCondition = "";
                if (!this.isBlank(this.searchCondition)) {
                    srchCondition = srchCondition + this.searchCondition;
                }
                if (typeof vueObj.conditionForInstantSearch != 'undefined')
                {
                    srchCondition = vm.conditionForInstantSearch;
                } 
                  
            var url = `pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&searchCondition=${srchCondition}`;

            await DataServices.FetchTicketStatusList(url).then(function (response) {
                // debugger;
                if (response.data != "" && response.data != null) {
                    vueObj.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "status_id") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                    });
                    if (response.data.result.data.length > 0) {
                        // debugger
                        vueObj.MapStatusListing = response.data.result.data;
                        vueObj.TotalRecords = response.data.result.data[0].total_records;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.listheaderbuttons[1].isdisabled = true;
                        vueObj.noRecord = false;
                        // vueObj.IsLoad = true;
                    } 
                    else {
                        vueObj.MapStatusListing = response.data.result.data;
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0                       
                    }
                }
                else {
                    vueObj.noRecord = true;
                    vueObj.Headers = [];
                    vueObj.TotalRecords = 0
                }
                // setTimeout(function () { vueObj.CheckBoxBootstrap() }, 100);
                 setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
            })
        },
        commonHeaderButtonClick: function (event) {
            // debugger;
            switch (event.callbackfunction) {
                case "openMapStatus":
                    this.openAddMapStatus(null);
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;              
            }
        },       
        getStatusClassList: function (status) {
           // debugger;
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        openAddMapStatus: function (id) {    
            if(id == undefined)
                    id = 0;
                this.mapStatusId = id;           
                this.AddMapStatusPopup = !this.AddMapStatusPopup;
            },
        commonDeleteData: function (event) {
          //  debugger;
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Resource")), function () {
                 //   debugger;
                    var postJSON = {
                        ids: val,
                        companyId: vm.companyId,
                        userId: vm.user_id,
                        moduleName: "FSM",
                        refCode: "TICKET_MAP_STATUS"

                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.deleteActivityData(objectPostString).then(response => {
                    //    debugger
                        var box = "";
                        if (response.data[0].Status == "Success") {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $("[id^='DeleteItem']").removeClass('enable').addClass('disabled');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            vm.FetchData();
                            box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('StatusMapping')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('StatusMapping')]), "failure", true, vm.$t('Alert'));
                        }
                        setTimeout(function () {
                            box.modal('hide');
                        }, 2000);

                    });
                });
            }
            else {
                alert(vm.$t("Selectanyrecordtodelete"));
            }
        },
        UpdateStatus: async function(status){
        //debugger
        var vm =this;
        if(status=='Active'){
            status=1001
        }
        else{
            status=1002
        }
        var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("Confirm"), true, (vm.$t("update") + "  " + vm.$t("status")), function () {
                var url = `id=${val}&status_id=${status}&isFor=${'group'}`;
                DataServices.updatestatus(url).then(response => {
                  //  debugger
                    if(response.data==true){
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked'); 
                        vm.FetchData();                        
                    }
                      
                })

           
                })
            }
        }
    }


 }

</script>